<template>
  <div class="DisciplineCompetitionTotalBackground">
    <!--     <input
            type="text"
            class="DisciplineCompetitionUserNumberInput"
            v-model="DisciplineCompetitionUserNumber"
            placeholder="请输入学生学号"
          />
          <button  class="DisciplineCompetitionSeekButton" @click="DisciplineCompetitionSeekButtonClick">查询</button>
          <button  class="DisciplineCompetitionEditButton" @click="DisciplineCompetitionEditButtonClick">编辑</button>
       -->
    <div class="DisciplineCompetitionTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 2%">姓名</th>
            <th style="width: 5%">证书名</th>
            <th style="width: 3%">证书编号</th>
            <th style="width: 2%">获奖级别</th>
            <th style="width: 2%">参加形式</th>
            <th style="width: 2%">排名</th>
            <th style="width: 2%">获得时间</th>
            <th style="width: 3%">举办单位</th>
            <th style="width: 3%">证书照片</th>
            <th style="width: 3%">相关文件</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="records in StudentDisciplineCompetitionTable"
            :key="records.studentNumber"
          >
            <td>{{ records.subjectCompetitions[0].studentName }}</td>
            <td>{{ records.subjectCompetitions[0].certificateName }}</td>
            <td>{{ records.subjectCompetitions[0].certificateNumber }}</td>
            <td>{{ records.subjectCompetitions[0].awardLevel }}</td>
            <td>{{ records.subjectCompetitions[0].participationForm }}</td>
            <td>{{ records.subjectCompetitions[0].rank }}</td>
            <td>{{ records.subjectCompetitions[0].getTime }}</td>
            <td>{{ records.subjectCompetitions[0].organizer }}</td>
            <td
              @click="
                pictureInspectClick(
                  records.subjectCompetitions[0].certificationPhoto
                )
              "
            >
              <img
                :src="records.certificationPhoto"
                alt="获奖照片"
                width="100"
                height="30"
              />
            </td>
            <td>
              <a
                href="{{ records.subjectCompetitions[0].signUpFile }}"
                target="_blank"
                >点击查看文档</a
              >
            </td>
            <td @click="ViewDetails(records)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div v-if="ifDetails" class="DisciplineCompetitionDetailsBackground">
      <div class="DisciplineCompetitionDetailsFormBackground">
        <span class="DisciplineCompetitionDetailsFormTitle"
          >学生奖项详细信息</span
        >
        <form class="DisciplineCompetitionDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>荣誉证书名 : {{ data.certificateName }}</label>
            </div>
            <div class="label-row">
              <label>荣誉证书编号 : {{ data.certificateNumber }}</label>
            </div>
            <div class="label-row">
              <label>获奖级别 : {{ data.awardLevel }}</label>
            </div>
            <div class="label-row">
              <label>参加形式 : {{ data.participateForm }}</label>
              <label>排名 : {{ data.rank }}</label>
            </div>
            <div class="label-row">
              <label>获得时间 : {{ data.getTime }}</label>
            </div>
            <div class="label-row">
              <label>举办单位 : {{ data.organizer }}</label>
            </div>

            <div class="label-row">
              <label>证书照片 : </label>
              <label
                ><img
                  :src="data.certificationPhoto"
                  alt="图片描述"
                  width="400"
                  height="300"
              /></label>
            </div>

            <div class="label-row">
              <label>相关文档 : </label>
              <a href="{{  data.signUpFile }}" target="_blank">{{
                data.signUpFile
              }}</a>
            </div>
            <br />
          </div>
        </form>
        <button
          class="DisciplineCompetitionDetailsFormCloseButton"
          @click="DisciplineCompetitionDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>

    <div class="DisciplineCompetitionPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>

    <div
      v-if="ifPicture"
      class="DisciplineCompetitionDetailsBackground"
      @click="DisciplineCompetitionDetailsBackgroundClick"
    >
      <img :src="this.pictureURL" alt="图片描述" width="70%" height="300" />
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      StudentDisciplineCompetitionTable: [],
      studentDetails: {},
      ifPicture: false,
      ifDetails: false,
      pictureURL: "",
      currentPage: 1,
      pageSize: 12,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    formatDate(date) {
      const d = new Date(date);
      return `${d.getFullYear()}-${(d.getMonth() + 1)
        .toString()
        .padStart(2, "0")}-${d.getDate().toString().padStart(2, "0")}`;
    },
    pictureInspectClick(picture) {
      this.pictureURL = picture;
      this.ifPicture = true;
    },
    DisciplineCompetitionDetailsBackgroundClick() {
      this.ifPicture = false;
    },
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getSubjectCompetition",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.StudentDisciplineCompetitionTable =
            informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },

    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },

    RenewLabel(studentRecord) {
      this.studentDetails = studentRecord.subjectCompetitions;
    },
    DisciplineCompetitionDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.DisciplineCompetitionPagination {
  position: absolute;
  top: 95%;
}
.DisciplineCompetitionUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.DisciplineCompetitionTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.DisciplineCompetitionTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/DisciplineCompetitionForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
